.card {
    position: relative;

}

.card img {
    width: 100%;
    display: block;
    border: 2px solid white;
    border-radius: 6px;
}

/* front of card , the picture */

.card .front {
    transform: rotateY(90deg);
    transition:all ease-in 0.2s;
    position: absolute;
}

.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/* back of card - cover */

.flipped .back {
    transform: rotatey(90deg);
    transition-delay: 0s;
}